import React from 'react';
import CardCustom from './cardcustom';

import featureImage from '../../assets/images/best-features.webp'


const Features = (props) => {

    let features = [

        {
            featureTitle: 'Easy to Costomize',
            featureDesc: 'Aorem psum olorsit amet ectetur adipiscing elit, sed dov.'
        },
        {
            featureTitle: 'Extreme Security',
            featureDesc: 'Aorem psum olorsit amet ectetur adipiscing elit, sed dov.'
        },
        {
            featureTitle: 'Customer Support',
            featureDesc: 'Aorem psum olorsit amet ectetur adipiscing elit, sed dov.'
        },
        {
            featureTitle: 'Creative Design',
            featureDesc: 'Aorem psum olorsit amet ectetur adipiscing elit, sed dov.'
        }        

    ]

    return (
        <div className="features" id={props.id}>
            <div className="container">
                <div className="row">
                    <div className="col-md-4">
                        <div className="feature__image">
                            <img src={featureImage} className="img-fluid" alt="feature"/>
                        </div>
                    </div>

                    <div className="col-md-8">
                        <div className="feature__list">
                            <div className="headign__main">
                                <h2>Some of the best features Of Our App!</h2>
                            </div>
                            <div className="row">
                                {features.map(( features, i )=> (
                                    <CardCustom col="col-md-6" key={i.toString()}>
                                        <div className="feature__icon">
                                            <span>
                                                <Icon fill="#fff"/>
                                            </span>
                                        </div>
                                        <div className="feature__caption">
                                            <h3>{features.featureTitle}</h3>
                                            <p>{features.featureDesc}</p>
                                        </div>
                                    </CardCustom>
                                ))}
                                
                            </div>
                        </div>

                    </div>

                </div>
            </div>
        </div>
    )

}



